@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.ttf");
  font-weight: 400;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-SemiBold.ttf");
  font-weight: 600;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Bold.ttf");
  font-weight: 700;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-ExtraBold.ttf");
  font-weight: 800;
}
html {
  font-size: 62.5%;
}

p,
h1,
h2,
h3,
h4,
h5,
h6,
span,
a,
li {
  font-family: "Inter", sans-serif;
  font-weight: 100;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-wrap: inherit !important;
}

a {
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

.nav-container {
  color: #fff;
  width: 100%;
  height: 122px;
  padding: 0px 130px 30px 130px;
  background: linear-gradient(180deg, #1A4A8C -3.27%, rgba(0, 189, 214, 0.33) 100%), url(../assets/vuelos-desk.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.3s all ease;
}
.nav-container__menu {
  display: none;
  cursor: pointer;
}
.nav-container__options {
  width: 375px;
}
.nav-container__options ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-container__options li {
  list-style: none;
}
.nav-container__options li a {
  text-decoration: none;
  color: #fff;
  font-size: 1.4rem;
  font-weight: 700;
}

.nav-mobile {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  background: #DEF3FF;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 1s ease left;
  z-index: 999;
  display: none;
}
.nav-mobile__container {
  margin: 0px auto;
  width: 90%;
  min-height: 95vh;
}
.nav-mobile__container--close {
  height: 80px;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0px 20px;
}
.nav-mobile__container--close img {
  cursor: pointer;
}
.nav-mobile__container--user {
  width: 100%;
  height: 80px;
  border-bottom: solid 2px #559FC7 opacity(0.4);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0px 10px;
}
.nav-mobile__container--user span {
  font-size: 1.4rem;
  font-weight: 700;
  color: #1A4A8C;
  margin-left: 10px;
}
.nav-mobile__container hr {
  margin-bottom: 30px;
  opacity: 0.4;
  border: 2px solid #559FC7;
}
.nav-mobile__container hr:last-child {
  margin-top: 30px;
}
.nav-mobile__container--options {
  height: 500px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
}
.nav-mobile__container--options_item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0px 10px;
  color: #1A4A8C;
  width: 100%;
  height: 36px;
  border-radius: 5px;
}
.nav-mobile__container--options_item img {
  fill: #1A4A8C !important;
}
.nav-mobile__container--options_item span {
  margin-left: 20px;
  font-weight: 400;
  font-size: 1.4rem;
}
.nav-mobile__container--options_item:hover {
  background: #00BDD6;
  color: #fff;
  cursor: pointer;
  transition: 0.5s all ease;
}
.nav-mobile__container--options_item:hover img {
  transition: 0.5s all ease;
  filter: hue-rotate(60deg) brightness(1000000%) !important;
}
.nav-mobile__container--carrito {
  width: 100%;
  height: 80px;
  border-bottom: solid 2px #559FC7 opacity(0.4);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0px 10px;
}
.nav-mobile__container--carrito span {
  font-size: 1.4rem;
  font-weight: 700;
  color: #1A4A8C;
  margin-left: 10px;
}

.menuActive {
  left: 0;
}

.itemActive {
  background: #00BDD6;
  color: #fff;
}
.itemActive img {
  filter: hue-rotate(60deg) brightness(1000000%) !important;
}

@media (max-width: 992px) {
  .nav-container {
    padding: 30px 30px 0px 30px;
  }
  .nav-container__menu {
    display: block;
  }
  .nav-container__options {
    display: none;
  }
  .nav-mobile {
    display: block;
  }
}
.footer {
  min-height: 600px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #F7F7F7;
}
.footer__container {
  padding: 30px 0px;
  width: 80%;
  min-height: 300px;
  background: #F7F7F7;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}
.footer__container--logo {
  width: 155px;
  margin-right: 100px;
}
.footer__container--logo_social {
  margin-top: 10px;
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__container--options {
  width: 110px;
}
.footer__container--terms {
  width: 218px;
}
.footer__container--privacy {
  width: 155px;
}
.footer__container--info {
  width: 155px;
}
.footer__container--info h3 {
  color: #4D4D4D;
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 14px;
}
.footer__container p {
  color: #4D4D4D;
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 14px;
}
.footer__container ul {
  list-style: none;
}
.footer__container ul li {
  font-size: 1.2rem;
  font-weight: 500;
  line-height: 16px;
  color: #4D4D4D;
  margin-bottom: 5px;
}
.footer__logos {
  width: 100%;
  min-height: 80px;
  background: #EAEAEA;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
.footer__logos-1, .footer__logos-2 {
  width: 400px;
  height: 43px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}
.footer__logos-1 p, .footer__logos-2 p {
  color: #4D4D4D;
  font-size: 1.2rem;
  font-weight: 500;
}
.footer__logos-text {
  display: none;
}
.footer__data {
  min-height: 220px;
  width: 100%;
  background: #fff;
  text-align: center;
  padding: 50px 0px;
}
.footer__data-1 p, .footer__data-2 p {
  padding: 20px 40px;
  color: #999999;
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 16px;
}

@media (max-width: 992px) {
  .footer__logos-1 {
    width: 320px;
  }
  .footer__logos-1 img {
    width: 70px;
  }
  .footer__logos-2 {
    width: 320px;
  }
  .footer__logos-2 img {
    width: 40px;
  }
  .footer__container {
    justify-content: flex-start;
    gap: 30px;
  }
}
@media (max-width: 576px) {
  .footer__logos {
    padding: 30px 0px;
    flex-direction: column;
  }
  .footer__logos-2 p {
    display: none;
  }
  .footer__logos-text {
    display: block;
    padding: 20px 0px 10px;
    color: #999999;
    font-weight: 400;
    font-size: 1.2rem;
    width: 320px;
  }
}
.preloader {
  background: #1a4a8c;
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader {
  animation: entry 10s ease-out infinite;
}

@keyframes entry {
  0% {
    opacity: 0;
    transform: scale(1.8) translateY(-0.75em);
  }
  20% {
    opacity: 1;
  }
  89% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: translateZ(-1000em);
  }
}
.header-thanks {
  position: relative;
  width: 100%;
  height: 380px;
  background: black;
  margin-bottom: 30px;
}
.header-thanks__txt {
  position: absolute;
  left: 50px;
  bottom: 50px;
  color: #fff;
}
.header-thanks__txt h1 {
  font-weight: 800;
  font-size: 3.6rem;
}
.header-thanks__txt p {
  font-weight: 800;
  font-size: 1.6rem;
}

.resume {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #1A4A8C;
  margin-bottom: 20px;
}
.resume-cont {
  width: 90%;
  padding-top: 20px;
}
.resume-cont__data {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  gap: 15vw;
}
.resume-cont__data--item {
  width: 240px;
}
.resume-cont__data--item p,
.resume-cont__data--item h3 {
  width: 100%;
}
.resume h2:first-child {
  margin-bottom: 20px;
  font-weight: 800;
  font-size: 2rem;
}
.resume h2 {
  font-weight: 800;
  font-size: 2rem;
}
.resume p {
  font-weight: 500;
  font-size: 1.6rem;
}

.more {
  margin-top: 40px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.more-cont {
  width: 90%;
  color: #1A4A8C;
  margin-bottom: 50px;
}
.more-cont__cards {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-top: 20px;
  text-align: left;
}
.more-cont__cards--item {
  width: 328px;
}
.more-cont h2 {
  font-weight: 800;
  font-size: 2.4rem;
}
.more-cont h3 {
  margin: 10px 0px;
  font-weight: 800;
  font-size: 2rem;
}
.more-cont p {
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 20px;
}

@media (max-width: 1100px) {
  .more-cont__cards {
    justify-content: center;
    gap: 20px;
  }
  .more-cont__cards--item {
    width: 250px;
  }
  .more-cont__cards img {
    width: 250px;
  }
}
@media (max-width: 992px) {
  .more {
    text-align: center;
  }
  .resume-cont {
    text-align: left;
  }
  .resume-cont__data {
    justify-content: flex-start;
    gap: 50px;
  }
}
@media (max-width: 576px) {
  .more {
    text-align: center;
  }
  .more-cont__cards--item {
    width: 335px;
  }
  .more-cont__cards--item img {
    width: 335px;
  }
  .resume-cont__data {
    justify-content: flex-start;
    gap: 50px;
  }
}